"use client";

import React, { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { Sidebar, SidebarBody, SidebarLink } from "@/components/ui/components/ui/sidebar";
import { motion } from "motion/react";
import { cn } from "@/lib/utils";
import { usePathname } from "next/navigation";
import { UserButton } from "@clerk/nextjs";

import Dashboard from "../page";


// Icon
import {
  LayoutGrid,
  PiggyBank,
  ReceiptText,
  ShieldCheck,
  CircleDollarSign,
} from "lucide-react";

export default function SidebarDemo() {
  const path = usePathname();
  const [open, setOpen] = useState(false);

  const links = [
    {
      label: "Dashboard",
      href: "/dashboard",
      icon: <LayoutGrid />,
    },
    {
      label: "Expenses",
      href: "/dashboard/expenses",
      icon: <ReceiptText />,
    },
    {
      label: "Budget",
      href: "/dashboard/budget",
      icon: <ShieldCheck />,
    },
    {
      label: "Saving Wallet",
      href: "/dashboard/saving-wallet",
      icon: <PiggyBank />,
    },
    {
      label: "Your Debt",
      href: "/dashboard/your-debt",
      icon: <CircleDollarSign />,
    },
  ];

  return (
    <div
      className={cn(
        "mx-auto flex w-full max-w-7xl flex-1 flex-col overflow-hidden rounded-md border border-neutral-200 bg-gray-100 md:flex-row dark:border-neutral-700 dark:bg-neutral-800",
        "h-screen", // for your use case, use `h-screen` instead of `h-[60vh]`
      )}
    >
      <Sidebar open={open} setOpen={setOpen}>
        <SidebarBody className="justify-between gap-10">
          <div className="flex flex-1 flex-col overflow-x-hidden overflow-y-auto">
            {open ? <Logo /> : <LogoIcon />}
            <div className="mt-8 flex flex-col gap-2">
              {links.map((link, idx) => (
                <SidebarLink
                  key={idx}
                  link={link}
                  className={path === link.href ? "text-primary bg-blue-100" : ""}
                />
              ))}
            </div>
          </div>
          <div>
            <SidebarLink
              link={{
                label: "Manu Arora",
                href: "#",
                icon: (
                  <Image
                    src="/LogoWeb-removebg-preview.png"
                    className="h-7 w-7 shrink-0 rounded-full"
                    width={50}
                    height={50}
                    alt="Avatar"
                  />
                ),
              }}
            />
          </div>
        </SidebarBody>
      </Sidebar>
      <Dashboard />
    </div>
  );
}

// ====================== LOGO ====================== //
export const Logo = () => {
  return (
    <a
      href="/LogoWeb-removebg-preview.png"
      className="relative z-20 flex items-center space-x-2 py-2 text-sm font-normal text-black"
    >
      <div className="h-5 w-6 shrink-0 rounded-tl-lg rounded-tr-sm rounded-br-lg rounded-bl-sm bg-black dark:bg-white" />
      <motion.span
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="font-semibold text-lg whitespace-pre text-black dark:text-white"
      >
        Finman
      </motion.span>
    </a>
  );
};

export const LogoIcon = () => {
  return (
    <a
      href="/LogoWeb-removebg-preview.png"
      className="relative z-20 flex items-center space-x-2 py-2 text-sm font-normal text-black"
    >
      <div className="h-5 w-6 shrink-0 rounded-tl-lg rounded-tr-sm rounded-br-lg rounded-bl-sm bg-black dark:bg-white" />
    </a>
  );
};

